.order {
    position: relative;
    width: 36em;
    background-color: #f8f7f7;
    height: 15em;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5vh;
    border-radius: 15px;
}

.left-part-order {
    position: relative;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    height: 100%;
    width: 12em;
}

.order-icon {
    position: relative;
    width: 70%;
    margin-top: 2%;
}

.mmr-under-icon {
    position: relative;
    top: 0.5em;
    font-size: 200%;
    text-align: center
}

.timer {
    position: relative;
    text-align: center;
    padding-top: .3em;
    line-height: 1.3em;
    font-size: 2em;
}
#login-register-form{
    width: 240px;
    margin: auto;
    margin-top: 3em;
    /* width: 15%; */
}