.scrolling-items-img {
    margin-top: 5px;
    width: 100%;
}

.Immortal {
    color: #e4ae39;
}

.Rare {
    color: #4b69ff
}

.Arcana{
    color: #ade55c;
}

.Legendary{
    color: #d32ce6
}

.scrolling-items {
    width: 200px;
}

.grid-card {
    display: grid;
    margin-top: 2px;
    grid-template-columns: 50% 50%;
    grid-gap: 0px;
}

.account-card{
    transition: all .3s ease-in-out; 
}

.account-card:hover {
    transform: scale(1.04);
}

.box {
    text-align: center;
    padding: 1px;
}

.grid-card-rating {
    text-align: center;
}